<template>
	<div>
		<div class="vmc-flex vmc-flex-rstart" style="margin-top: 20px;padding-left: 20px;">
			<router-link class="vmc-block btn-sm" to="/">返回</router-link>
		</div>
		<div class="vmc-flex vmc-flex-center vmc-flex-column" :style="containerStyles()">
            <input type="text" placeholder="text" v-model="inputText" class="vmc-text-center input-text vmc-block" @focus="onFucus" @blur="onBlur"/>
            <input type="password" placeholder="password" v-model="inputText" class="vmc-text-center input-text vmc-block" @focus="onFucus" @blur="onBlur"/>
            <input type="textarea" placeholder="textarea" v-model="inputText" class="vmc-text-center input-text vmc-block" @focus="onFucus" @blur="onBlur"/>
			<!--<a class="vmc-block btn-sm btn-long" href="javascript:;" @click="showKeyboard = true">弹出</a>
			<a style="margin-top: 20px;" class="vmc-block btn-sm btn-long" href="javascript:;" @click="showKeyboard = false">收起</a> -->
		</div>
        <vmc-number-keyboard
            v-model="showKeyboard"
            @show="onShow"
            @hide="onHide"
            @blur="showKeyboard = false"
            @select="onSelect"
            @delete="onDelete"
        />
	</div>
</template>


<script>
	export default {
		data () {
			return {
                showKeyboard: false,
                inputText: '',
                inputText2: '',
                inputText3: ''
			}
		},
		methods: {
            onFucus() {
                this.showKeyboard = true
            },
            onBlur() {
                 this.showKeyboard = false
            },
			containerStyles() {
				return { height:  document.documentElement.clientHeight * 0.6 + 'px'}
			},
            onSelect(v) {
                this.inputText += v
            },
            onDelete() {
                this.inputText = this.inputText.slice(0, this.inputText.length - 1)
            },
            show(){

            },
            hide() {

            },
            onShow() {
                console.log('show')
            },
            onHide() {
                console.log('hide')
            }
		}
	}
</script>

<style scoped lang="less">
.btn-sm{
	border-radius: 2px;
	padding: 0.08rem 0.2rem;
	background-color: #fff;
	-webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
}
.btn-long{
	width: 90%;
	text-align: center;
}
.input-text{
    width: 90%;
    height: 40px;
    margin-bottom: 20px;
}
</style>



